/* App.css - Art Haven Dark Theme (Final Version) */

:root {
  --bg-dark: #0f1115;       /* Main Background */
  --bg-card: #181b21;       /* Card Background */
  --primary-blue: #4a90e2;  /* The Blue Button Color */
  --text-white: #ffffff;
  --text-gray: #a0a0a0;
  --border-color: #2a2e35;
  --radius: 8px;
}

body {
  margin: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  background-color: var(--bg-dark);
  color: var(--text-white);
  -webkit-font-smoothing: antialiased;
}

/* --- Navigation Bar --- */
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  background-color: var(--bg-dark); 
  border-bottom: 1px solid var(--border-color);
  
  /* Sticky Navbar Logic */
  position: sticky;
  top: 0;
  z-index: 1000; 
}

.logo {
  font-size: 20px;
  font-weight: 700;
  color: var(--text-white);
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 10px;
}

.nav-links {
  display: flex;
  align-items: center;
  gap: 20px;
}

/* Navigation Link Styles */
.nav-item {
  text-decoration: none;
  color: #a0a0a0; /* Default Gray */
  font-weight: 500;
  transition: color 0.2s;
  margin-right: 20px;
}

.nav-item:hover {
  color: #4a90e2; /* Blue on hover */
}

.nav-item.active {
  color: #4a90e2; /* Blue when active */
}

/* --- Profile Icon (Navbar) --- */
/* This handles both the default icon AND the uploaded user photo */
.nav-profile-icon {
  width: 40px;       
  height: 40px;      
  min-width: 40px;   
  min-height: 40px;
  background-color: var(--primary-blue);
  color: white;
  border-radius: 50%; /* Perfect Circle */
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  text-decoration: none;
  transition: transform 0.2s;
  overflow: hidden; /* Cuts off any image parts outside the circle */
  border: 2px solid transparent;
  padding: 0;
}

.nav-profile-icon img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Zooms in to fill the circle perfectly */
  display: block;
}

.nav-profile-icon:hover {
  transform: scale(1.1);
  border-color: white;
}

/* --- Buttons --- */
.btn {
  padding: 10px 20px;
  border-radius: var(--radius);
  font-weight: 600;
  text-decoration: none;
  cursor: pointer;
  border: none;
  font-size: 14px;
  transition: all 0.2s;
}

.btn-primary {
  background-color: var(--primary-blue);
  color: white;
}

.btn-primary:hover {
  background-color: #357abd;
}

.btn-secondary {
  background-color: transparent;
  color: var(--text-white);
  border: 1px solid var(--border-color);
}

.btn-secondary:hover {
  border-color: var(--text-white);
}

.btn-danger {
  background-color: #e63946;
  color: white;
  width: 100%;
  margin-top: 10px;
}

/* --- Forms (Login/Signup/Upload) --- */
.form-box {
  max-width: 400px;
  margin: 60px auto;
  padding: 40px;
  background-color: var(--bg-card);
  border-radius: 12px;
  border: 1px solid var(--border-color);
  box-shadow: 0 4px 20px rgba(0,0,0,0.3);
}

.form-box h2 {
  margin-top: 0;
  color: var(--text-white);
  font-size: 24px;
  margin-bottom: 10px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}

label {
  font-size: 14px;
  color: var(--text-gray);
  margin-bottom: 5px;
  display: block;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="file"] {
  width: 100%;
  padding: 12px;
  background-color: var(--bg-dark);
  border: 1px solid var(--border-color);
  border-radius: var(--radius);
  color: white;
  font-size: 15px;
  box-sizing: border-box;
}

input:focus {
  outline: none;
  border-color: var(--primary-blue);
}

/* --- Hero Section (Landing Page) --- */
.hero {
  text-align: center;
  padding: 80px 20px;
  max-width: 800px;
  margin: 0 auto;
}

.hero h1 {
  font-size: 56px;
  font-weight: 800;
  margin-bottom: 20px;
  line-height: 1.1;
  background: -webkit-linear-gradient(white, #a0a0a0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.hero p {
  font-size: 18px;
  color: var(--text-gray);
  margin-bottom: 40px;
  line-height: 1.6;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 60px;
}

/* --- Grid Layout --- */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.masonry-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)); 
  gap: 25px;
}

.pin-card {
  background-color: var(--bg-card);
  border-radius: 12px;
  overflow: hidden;
  transition: transform 0.2s;
  border: 1px solid var(--border-color);
}

.pin-card:hover {
  transform: translateY(-5px);
  border-color: var(--text-gray);
}

.pin-image {
  width: 100%;        
  height: 300px;      /* FORCE all images to be exactly 300px tall */
  object-fit: cover;  /* Fills the box completely without stretching */
  display: block;
  border-bottom: 1px solid var(--border-color);
}

.pin-info {
  padding: 15px;
}

.pin-title {
  font-size: 16px;
  font-weight: 600;
  margin: 0 0 5px 0;
  color: var(--text-white);
}

.pin-author {
  font-size: 14px;
  color: var(--text-gray);
  margin: 0;
}

/* --- Profile Header (Dashboard/Profile Page) --- */
.profile-header {
  text-align: center;
  padding: 60px 0;
  background-color: var(--bg-dark);
}

.profile-avatar {
  width: 100px;
  height: 100px;
  background-color: var(--bg-card);
  border: 2px solid var(--primary-blue);
  color: var(--primary-blue);
  border-radius: 50%;
  margin: 0 auto 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  font-weight: bold;
}


/* --- Footer Section --- */
.footer {
  background-color: var(--bg-card); /* Matches the card color */
  border-top: 1px solid var(--border-color);
  padding: 60px 0 20px;
  margin-top: 80px; /* Pushes footer down from content */
  color: var(--text-gray);
}

.footer-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 40px;
}

.footer-col {
  flex: 1;
  min-width: 250px;
}

.footer-col h3 {
  color: var(--text-white);
  font-size: 18px;
  margin-bottom: 20px;
}

.footer-col p {
  line-height: 1.6;
  font-size: 14px;
}

.footer-col ul {
  list-style: none;
  padding: 0;
}

.footer-col ul li {
  margin-bottom: 10px;
}

.footer-col ul li a {
  color: var(--text-gray);
  text-decoration: none;
  transition: color 0.2s;
}

.footer-col ul li a:hover {
  color: var(--primary-blue);
}

/* Social Icons */
.social-links {
  display: flex;
  gap: 15px;
}

.social-links a {
  color: var(--text-white);
  font-size: 20px;
  transition: transform 0.2s, color 0.2s;
  background-color: var(--bg-dark);
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  text-decoration: none;
}

.social-links a:hover {
  color: var(--primary-blue);
  transform: translateY(-3px);
}

/* Copyright Area */
.footer-divider {
  border: 0;
  border-top: 1px solid var(--border-color);
  margin: 40px 0 20px;
}

.footer-bottom {
  text-align: center;
  font-size: 13px;
  color: #666;
}

/* --- Mobile Responsiveness --- */
@media (max-width: 768px) {
  /* Navbar */
  nav {
    padding: 15px 20px;
  }
  
  .logo {
    font-size: 18px;
  }

  /* Stack Footer */
  .footer-row {
    flex-direction: column;
    gap: 30px;
  }

  /* Grid: 2 Columns on Tablet, 1 Column on Phone */
  .masonry-grid {
    grid-template-columns: repeat(auto-fill, minmax(100%, 1fr)); /* Force 1 column on very small screens */
  }
  
  /* Hero Text Size */
  .hero h1 {
    font-size: 36px;
  }
}